<template>
    <div class="home">
        <img src="../assets/ewm.png" alt="" class="ewm">
        <div class="mian">
            <img src="../assets/logo.png" alt="" class="logo">
            <div class="dlx">
                <div class="dlx-img">
                    <img src="../assets/pic.png" alt="">
                </div>
                <div class="denglu">
                    <img src="../assets/text.png" alt="" style="margin-left: 60px;">
                    <div>
                        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm" status-icon
                            hide-required-asterisk style="width: 450px;">
                            <el-form-item prop="name" style="margin-bottom: 35px;">
                                <el-input v-model="ruleForm.name" placeholder="用户名"
                                    prefix-icon="el-icon-user"></el-input>
                            </el-form-item>
                            <el-form-item prop="pass" style="margin-bottom: 35px;">
                                <el-input placeholder="密码" v-model="ruleForm.pass" show-password
                                    prefix-icon="el-icon-lock"></el-input>
                            </el-form-item>
                            <el-form-item prop="yanz" style="text-align: left;margin-bottom: 35px;">
                                <el-input v-model="ruleForm.yanz" prefix-icon="el-icon-chat-square"
                                    placeholder="邮箱"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" style="width: 450px;"
                                    @click="submitForm('ruleForm')">立即注册</el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                    <a href="" @click.prevent="denl">立即登录</a>
                </div>
            </div>
        </div>
        <p
            style="position: absolute;bottom: 1%; left: 50%;transform: translate(-50%, 50%);width: 100%;font-size: 14px;">
            Copyright ©
            2018-2024 EMI. ALL
            rights reserved <a href="https://www.emicnet.com/">南京易米云通网络科技有限公司</a> 版权所有
            <a href="https://beian.miit.gov.cn/#/Integrated/index">苏ICP备14035390号-4</a>
        </p>
    </div>
</template>
<script>
import { zhuce } from "../api/https";
export default {
    data() {
        return {
            ma: '',
            n: '',
            mia: 60,
            ruleForm: {
                name: '',
                pass: '',
                yanz: '',
            },
            rules: {
                name: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                ],
                pass: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                ],
                yanz: [
                    { required: true, message: '请输入邮箱', trigger: 'blur' },
                    { type: 'email', message: '请输入正确的邮箱', trigger: ['blur', 'change'] }
                ],
            }
        };
    },
    created() {
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // console.log({
                    //     emails: this.ruleForm.yanz,
                    //     name: this.ruleForm.name,
                    //     pwd: this.ruleForm.pass,
                    // })
                    zhuce({
                        emails: this.ruleForm.yanz,
                        name: this.ruleForm.name,
                        pwd: this.ruleForm.pass,
                    })
                        // this.$axios({
                        //     method: "post",
                        //     url: "http://47.94.4.201/index.php/index/index/register",
                        //     data: {
                        //         emails: this.ruleForm.email,
                        //         name: this.ruleForm.name,
                        //         pwd: this.ruleForm.pass,
                        //     }
                        // })
                        .then((response) => {
                            console.log(response)
                            if (response.code == 0) {
                                this.$message({
                                    message: '注册成功',
                                    type: 'success'
                                });
                            } else if (response.code == 2) {
                                this.$message({
                                    message: '该用户名已被注册',
                                    type: 'error'
                                });
                            } else {
                                this.$message({
                                    message: '注册失败',
                                    type: 'error'
                                });
                            }
                        })
                        .catch((error) => {
                            console.error(error);
                        })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        denl() {
            console.log(1);
            this.$router.replace('/')
        }
    },
};
</script>

<style scoped>
